import { PropsWithChildren } from "react"
import { Box, CssBaseline, SwipeableDrawer, styled } from "@rp/react/web/mui/core"

const drawerBleeding = 56

const Root = styled("div", { name: "DrawerMobileRoot" })(({ theme }) => ({
  height: "100%",
  backgroundColor: theme.palette.elevation.dp06,
}))

const StyledBox = styled(Box)(({ theme }) => ({
  backgroundColor: theme.palette.elevation.dp16,
}))

const Puller = styled(Box)(({ theme }) => ({
  width: 125,
  height: 6,
  backgroundColor: theme.palette.grey[500],
  borderRadius: 3,
  margin: "16px auto 8px auto",
}))

type Props = PropsWithChildren<{ open: boolean; toggleDrawer: () => void }>

const DrawerBase = ({ open, toggleDrawer, children }: Props) => {
  return (
    <Root>
      <CssBaseline />
      <SwipeableDrawer
        anchor="bottom"
        open={open}
        onClose={() => toggleDrawer()}
        onOpen={() => toggleDrawer()}
        swipeAreaWidth={drawerBleeding}
        disableSwipeToOpen={true}
        ModalProps={{
          keepMounted: true,
        }}
      >
        <StyledBox
          sx={{
            position: "absolute",
            top: -drawerBleeding,
            borderTopLeftRadius: 8,
            borderTopRightRadius: 8,
            visibility: "visible",
            right: 0,
            left: 0,
          }}
        ></StyledBox>
        <StyledBox
          sx={{
            pb: 2,
            height: "100%",
            borderRadius: "16px 16px 0 0",
            overflow: "auto",
            "& > div": {
              px: 2,
              py: 1,
              display: "flex",
            },
          }}
        >
          <Box component={"span"}>
            <Puller />
          </Box>

          {children}
        </StyledBox>
      </SwipeableDrawer>
    </Root>
  )
}

export { DrawerBase }
